import React, { useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'

import queryString from 'query-string'
import { Instruments } from '../models/Instruments';
import Card from '../components/Card';


const SearchScreen = ({ history }) => {

    const location = useLocation()
    
    const {q = ""} = queryString.parse(location.search)

    const [inputValue, setInputValue] = useState(q)
    const [instruments, setInstruments] = useState([])

    const handleChange = (e) => {
        const value = e.target.value
        setInputValue(value)
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        history.push(`?q=${inputValue}`)
       
    }

    const getInstruments = () => {
        if (inputValue.trim() !== "") {
            const value = inputValue.toLocaleLowerCase()
            const newValue = Instruments.filter(instrument => instrument.name.toLocaleLowerCase().includes(value))
            
            setInstruments(newValue)
            
        } else {
            setInstruments([])
        }

    }

    useEffect(() => {
        getInstruments()
    }, [q])


    return (
        <div className="container">
            <h1>Search Screen</h1>
            <hr />
            <div className="row">
                <div className="col-6">
                    <h2>Search</h2>
                    <form onSubmit={handleSubmit} >
                        <label className="form-label w-100">
                            Instrument: {" "}
                            <input
                                placeholder="Name Instrument"
                                type="text"
                                className="form-control"
                                autoComplete="off"
                                value={inputValue}
                                onChange={handleChange}
                            />
                        </label>
                        <button type="submit" className="btn btn-danger text-dark w-100">Search</button>
                    </form>
                </div>
                <div className="col-6">
                    <h2>Results: {instruments.length}</h2>
                    {instruments.length === 0 && (<div className="alert alert-warning text-center">Please Search an Instrument</div>)}
                    {instruments.map(instrument => <Card key={instrument.id} {...instrument}/>)}
                </div>
            </div>
        </div>
    )
}

export default SearchScreen
